.subtotal {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 300px;
    height: 100px;
    padding: 20px;
    background-color: #f3f3f3;
}

.subtotal_gift {
    display: flex;
    align-items: center;
}

.subtotal > input {
    margin-right: 5px;
}

.subtotal > button {
    background-color: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
}

/* *********************** Media Queries ************************** */
@media only screen and (max-width: 400px){
    .subtotal { 
        width: 100%;
    } 
}